section {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(1, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px; }
  @media (min-width: 1200px) {
    section {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(1, 1fr);
      height: auto; } }

.img-responsive {
  width: 100%;
  height: auto; }

ul {
  width: 100%;
  font-family: Roboto, sans-serif; }
  ul.list-unstyled {
    padding: 0;
    margin: 0;
    list-style: none; }
  ul.twoColList {
    display: grid;
    grid-template-columns: repeat(1, 1fr); }
    @media (min-width: 600px) and (orientation: portrait) {
      ul.twoColList {
        grid-template-columns: repeat(2, 1fr); } }
    @media (min-width: 600px) and (orientation: landscape) {
      ul.twoColList {
        grid-template-columns: repeat(2, 1fr); } }
    ul.twoColList li {
      display: flex;
      align-items: center;
      justify-content: start; }
  ul.list-icons {
    display: flex; }
    @media (min-width: 1200px) {
      ul.list-icons {
        justify-content: flex-end; } }

.btn {
  text-align: center;
  text-transform: uppercase;
  height: 4rem;
  width: 9rem;
  background: none;
  line-height: 4rem;
  background: none;
  border: 0.1rem solid rgba(144, 144, 144, 0.25);
  outline: none;
  border-radius: 0.5rem;
  letter-spacing: 0.1rem;
  font-size: 0.9rem;
  font-weight: 600;
  transition: 1s all; }
  .btn:hover {
    border: 0.1rem solid #49fcd4;
    color: #49fcd4; }
  .btn a {
    text-decoration: none; }
  .btn__background {
    background: #49fcd4;
    border: none; }
    .btn__background:hover {
      background: #adfeec;
      border: none;
      color: initial;
      outline: none; }

#hero .hero--img {
  height: 100vh;
  background-image: url("./../images/pic01.jpg");
  background-position: center;
  background-size: cover; }

.hero--headlines, .section--headlines {
  background-image: url("./../images/lines.png");
  background-color: #49fcd4;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: unset;
  padding: 3rem 2rem;
  min-height: 25vh;
  display: flex; }
  @media (min-width: 600px) and (orientation: portrait) {
    .hero--headlines, .section--headlines {
      justify-content: right; } }
  @media (min-width: 600px) and (orientation: landscape) {
    .hero--headlines, .section--headlines {
      justify-content: right; } }
  @media (min-width: 1200px) {
    .hero--headlines, .section--headlines {
      padding: 0;
      justify-content: space-around;
      background-attachment: fixed;
      min-height: auto;
      text-align: right; } }
  .hero--headlines .container, .section--headlines .container {
    height: inherit;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative; }
    @media (min-width: 1200px) {
      .hero--headlines .container, .section--headlines .container {
        width: 40vw; }
        .hero--headlines .container::after, .section--headlines .container::after {
          content: "";
          position: absolute;
          right: 0;
          height: 50%;
          top: 50%;
          width: 0.2rem;
          background-color: #43d9b8; }
        .hero--headlines .container::before, .section--headlines .container::before {
          content: "";
          position: absolute;
          right: 0;
          width: 3rem;
          height: 0.2rem;
          background-color: #43d9b8; } }
    .hero--headlines .container div, .section--headlines .container div {
      position: relative;
      width: 100%; }
      @media (min-width: 1200px) {
        .hero--headlines .container div, .section--headlines .container div {
          padding-right: 5rem; }
          .hero--headlines .container div::after, .section--headlines .container div::after {
            content: "";
            position: absolute;
            width: 1rem;
            top: calc(50% - 0.5rem);
            height: 1rem;
            background-color: #43d9b8;
            border-radius: 50%;
            left: calc(100% - 3rem); } }
      .hero--headlines .container div a.scroll, .section--headlines .container div a.scroll {
        display: none; }
        @media (min-width: 1200px) {
          .hero--headlines .container div a.scroll, .section--headlines .container div a.scroll {
            display: block;
            writing-mode: vertical-rl;
            text-orientation: mixed;
            position: relative;
            font-size: 4rem;
            font-weight: 900; } }

.section--headlines .container {
  align-items: unset;
  font-family: 'Noto Sans JP', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .section--headlines .container div {
    height: inherit;
    display: flex;
    max-height: 100vh;
    flex-direction: column;
    justify-content: space-between; }
    .section--headlines .container div::after {
      top: 3.5rem; }
    .section--headlines .container div h4 {
      letter-spacing: 0.5rem; }
    .section--headlines .container div p {
      display: block; }
      @media (min-width: 1200px) {
        .section--headlines .container div p {
          display: block; } }
  .section--headlines .container::before {
    top: 4rem; }
  .section--headlines .container::after {
    top: 0;
    height: 100%; }

.section--content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5rem 3rem; }
  @media (min-width: 1200px) {
    .section--content {
      text-align: justify; } }
  .section--content--form {
    flex-wrap: wrap;
    justify-content: space-between; }
    .section--content--form input,
    .section--content--form textarea {
      margin: 0.5rem 0;
      border-radius: 0.325rem;
      border: solid 2px rgba(144, 144, 144, 0.25);
      color: inherit;
      display: block;
      outline: 0;
      padding: 0 1rem;
      text-decoration: none;
      height: 3.5rem;
      line-height: 2rem;
      width: 100%; }
      .section--content--form input:focus,
      .section--content--form textarea:focus {
        border: 0.1rem solid #49fcd4; }
    .section--content--form textarea {
      height: auto; }
    .section--content--form .btn {
      margin: 0.5rem 0; }

#last .section--headlines .container .last::after {
  right: -5.4rem;
  left: unset; }

#last .section--headlines .container::after {
  height: 85%; }

.clearBeforeAfter::after {
  content: "";
  width: 0px !important;
  height: 0px !important; }

.clearBeforeAfter::before {
  content: "";
  width: 0px !important;
  height: 0px !important; }
